.spacer {
  padding-top: 12.5rem;
}
$spacer: (
  '1': 12.5rem,
  '2': 10rem,
  '3': 7.5rem,
  '4': 5rem
);

@each $number, $size in $spacer {
  .spacer--#{$number},
  .spacer-up--#{$number} {
    padding-top: $size;
  }
  .spacer-down--#{$number} {
    padding-bottom: $size;
  }
}

$fixed-height: (
  '1': 2rem,
  '2': 4rem,
  '3': 6rem,
  '4': 8rem,
  '5': 10rem
);

@each $number, $height in $fixed-height {
  .fh-#{$number} {
    height: $height;
    max-height: $height;
    overflow: hidden;
  }
}

$icon-size: (
  'xs': 1rem,
  'sm': 2rem,
  'md': 4rem,
  'xl': 6rem
);

@each $size, $number in $icon-size {
  .icon-#{$size} {
    font-size: $number;
  }
}

